import { rewriteBlobby, unwriteBlobby } from '../../rewriters/url';

const originalCreateObjectURL = URL.createObjectURL.bind(URL);
const originalRevokeObjectURL = URL.revokeObjectURL.bind(URL);

URL.createObjectURL = new Proxy(originalCreateObjectURL, {
  apply(target, thisArg, args) {
    const url = Reflect.apply(target, thisArg, args);
    if (typeof url === 'string' && url.startsWith('blob:')) {
      const blob = rewriteBlobby(url);
      return blob;
    }
    return url;
  },
});

URL.revokeObjectURL = new Proxy(originalRevokeObjectURL, {
  apply(target, thisArg, args) {
    if (args.length > 0 && typeof args[0] === 'string') {
      args[0] = unwriteBlobby(args[0]);
    }
    return Reflect.apply(target, thisArg, args);
  },
});
