import { GetLocation } from '../../../rewriters/helper/getLocation';

const urlAttributes = {
  nonce: [HTMLElement],
  integrity: [HTMLScriptElement, HTMLLinkElement],
  csp: [HTMLIFrameElement],
  credentialless: [HTMLIFrameElement],
  src: [
    HTMLImageElement,
    HTMLMediaElement,
    HTMLIFrameElement,
    HTMLFrameElement,
    HTMLEmbedElement,
    HTMLScriptElement,
    HTMLSourceElement,
  ],
  href: [HTMLAnchorElement, HTMLLinkElement, SVGUseElement, SVGImageElement],
  data: [HTMLObjectElement],
  action: [HTMLFormElement],
  formaction: [HTMLButtonElement, HTMLInputElement],
  srcdoc: [HTMLIFrameElement],
  srcset: [HTMLImageElement, HTMLSourceElement],
  poster: [HTMLVideoElement],
  imagesrcset: [HTMLLinkElement],
};

const cspAttrs = new Set(['nonce', 'integrity', 'csp']);

function isFragment(url) {
  if (!url) return false;
  if (typeof url !== 'string') url = url.toString();
  if (url.startsWith('#')) return true;
  try {
    const u = new URL(url, 'http://example.com');
    return u.hash === url;
  } catch {
    return false;
  }
}

for (const [attr, elements] of Object.entries(urlAttributes)) {
  for (const Element of elements) {
    const descriptor = Object.getOwnPropertyDescriptor(Element.prototype, attr);
    Object.defineProperty(Element.prototype, attr, {
      get() {
        return self.__envade$rewrite.url.unwriteUrl(descriptor.get.call(this), GetLocation().href);
      },
      set(value) {
        if (isFragment(value)) {
          return descriptor.set.call(this, value);
        }
        value = self.__envade$rewrite.url.rewriteUrl(value, GetLocation().href);
        return descriptor.set.call(this, value);
      },
      configurable: true,
      enumerable: true,
    });
  }
}

const innerHTMLDescriptor = Object.getOwnPropertyDescriptor(Element.prototype, 'innerHTML');

Object.defineProperty(Element.prototype, 'innerHTML', {
  set(value) {
    const tag = this.tagName?.toLowerCase?.() ?? '';

    if (tag === 'script') {
      const rewritten = self.__envade$rewrite.javascript(value, GetLocation().href);
      return innerHTMLDescriptor.set.call(this, rewritten);
    }

    if (tag === 'style') {
      const rewritten = self.__envade$rewrite.css(value, GetLocation().href);
      return innerHTMLDescriptor.set.call(this, rewritten);
    }

    const parser = new DOMParser();
    const doc = parser.parseFromString(value, 'text/html');
    const rawHTML = doc.body.innerHTML;
    const rewrittenHTML = self.__envade$rewrite.html(rawHTML, GetLocation().href);
    return innerHTMLDescriptor.set.call(this, rewrittenHTML);
  },
  configurable: true,
  enumerable: true,
});

const outerHTMLDescriptor = Object.getOwnPropertyDescriptor(Element.prototype, 'outerHTML');

Object.defineProperty(Element.prototype, 'outerHTML', {
  set(value) {
    const tag = this.tagName?.toLowerCase?.() ?? '';

    if (tag === 'script') {
      const rewritten = self.__envade$rewrite.javascript(value, GetLocation().href);
      return outerHTMLDescriptor.set.call(this, rewritten);
    }

    if (tag === 'style') {
      const rewritten = self.__envade$rewrite.css(value, GetLocation().href);
      return outerHTMLDescriptor.set.call(this, rewritten);
    }

    const parser = new DOMParser();
    const doc = parser.parseFromString(value, 'text/html');
    const rawHTML = doc.documentElement.outerHTML;
    const rewrittenHTML = self.__envade$rewrite.html(rawHTML, GetLocation().href);
    return outerHTMLDescriptor.set.call(this, rewrittenHTML);
  },
  configurable: true,
  enumerable: true,
});

const srcSetDescriptor = Object.getOwnPropertyDescriptor(HTMLImageElement.prototype, 'srcset');
Object.defineProperty(HTMLImageElement.prototype, 'srcset', {
  get() {
    return srcSetDescriptor.get.call(this);
  },
  set(value) {
    value = self.__envade$rewrite.srcset(value, GetLocation().href);
    srcSetDescriptor.set.call(this, value);
  },
  configurable: true,
  enumerable: true,
});

const nativeGetAttribute = Element.prototype.getAttribute;
Element.prototype.getAttribute = function (attr) {
  if (cspAttrs.has(attr)) return null;
  return nativeGetAttribute.call(this, attr);
};

const nativeSetAttribute = Element.prototype.setAttribute;
Element.prototype.setAttribute = function (attr, value) {
  const loc = GetLocation().href;
  if (cspAttrs.has(attr)) {
    return nativeSetAttribute.call(this, attr, '');
  }
  if (urlAttributes[attr]) {
    if (!isFragment(value)) {
      value = self.__envade$rewrite.url.rewriteUrl(value, loc);
    }
  }
  if (attr === 'style') {
    value = self.__envade$rewrite.css(value, loc);
  }
  if (attr.includes('srcset')) {
    value = self.__envade$rewrite.srcset(value, loc);
  }
  return nativeSetAttribute.call(this, attr, value);
};

window.Audio = new Proxy(window.Audio, {
  construct(target, args, newTarget) {
    if (args[0]) {
      args[0] = self.__envade$rewrite.url.rewriteUrl(args[0], GetLocation().href);
    }
    return Reflect.construct(target, args, newTarget);
  },
});
