import { BareClient } from '@mercuryworkshop/bare-mux';
import * as pako from 'pako';

const client = new BareClient();
const decoder = new TextDecoder();
const suffix = new Uint8Array([0x00, 0x00, 0xff, 0xff]);

globalThis.WebSocket = new Proxy(globalThis.WebSocket, {
  construct(_target, args) {
    const [targetUrl, targetProtocols] = args;

    const bareSocket = client.createWebSocket(targetUrl, targetProtocols, null, {
      'User-Agent': self.navigator.userAgent,
      origin: self.scope$location.origin,
    });

    class RewriteSocket extends EventTarget {
      binaryType: 'blob' | 'arraybuffer' | 'json' = 'blob';
      bufferedAmount = 0;
      extensions = '';
      onclose: ((ev: CloseEvent) => any) | null = null;
      onerror: ((ev: ErrorEvent) => any) | null = null;
      onmessage: ((ev: MessageEvent) => any) | null = null;
      onopen: ((ev: Event) => any) | null = null;
      protocol = '';
      readyState = 0;
      url = targetUrl;
      #internal: any;

      constructor() {
        super();

        this.#internal = bareSocket;

        this.readyState = this.#internal.readyState;
        this.protocol = this.#internal.protocol;

        this.#internal.addEventListener('open', (e) => {
          this.readyState = 1;
          this.#emit('open', e);
        });

        this.#internal.addEventListener('close', (e) => {
          this.readyState = 3;
          this.#emit('close', e);
        });

        this.#internal.addEventListener('error', (e) => {
          this.#emit('error', e);
        });

        this.#internal.addEventListener('message', async (e) => {
          let payload = e.data;

          if (typeof payload === 'string') {
          } else if (payload instanceof Blob) {
            if (this.binaryType === 'arraybuffer') {
              payload = await payload.arrayBuffer();
              Object.setPrototypeOf(payload, ArrayBuffer.prototype);
            }
          } else if (payload instanceof ArrayBuffer || payload instanceof Uint8Array) {
            if (this.binaryType === 'blob') {
              payload = new Blob([payload]);
            } else {
              Object.setPrototypeOf(payload, ArrayBuffer.prototype);
            }
          }

          if (payload instanceof ArrayBuffer || payload instanceof Uint8Array) {
            const bytes = payload instanceof Uint8Array ? payload : new Uint8Array(payload);
            const hasSuffix = bytes.length >= 4 && bytes.slice(-4).every((b, i) => b === suffix[i]);

            if (hasSuffix) {
              try {
                const compressed = bytes.slice(0, -4);
                const decompressed = pako.inflateRaw(compressed);
                const decoded = decoder.decode(decompressed);
                this.#emit('message', new MessageEvent('message', { data: decoded }));
                return;
              } catch {}
            }

            try {
              const decoded = decoder.decode(bytes);
              if (decoded.startsWith('{') || decoded.startsWith('[')) {
                this.#emit('message', new MessageEvent('message', { data: decoded }));
                return;
              }
            } catch {}
          }

          this.#emit('message', new MessageEvent('message', { data: payload }));
        });
      }

      send(data: any) {
        this.#internal.send(data);
      }

      close(code?: number, reason?: string) {
        this.#internal.close(code, reason);
      }

      #emit(type: string, original: Event) {
        let event: Event;

        if (original instanceof MessageEvent) {
          event = new MessageEvent(type, { data: original.data });
        } else if (original instanceof CloseEvent) {
          event = new CloseEvent(type, {
            code: original.code,
            reason: original.reason,
            wasClean: original.wasClean,
          });
        } else if (original instanceof ErrorEvent) {
          event = new ErrorEvent(type, {
            message: original.message,
            error: original.error,
          });
        } else {
          event = new Event(type);
        }

        const proxyEvent = new Proxy(event, {
          get(target, prop) {
            if (prop === 'isTrusted') return true;
            return Reflect.get(target, prop);
          },
        });

        const handler = this['on' + type];
        if (typeof handler === 'function') {
          handler.call(this, proxyEvent);
        }

        this.dispatchEvent(event);
      }
    }

    return new RewriteSocket();
  },
});
