import { GetLocation } from '../../../rewriters/helper/getLocation';

const site = GetLocation().host;
let cookieCache = '';

async function refreshCookies() {
  const cookies = await self.__envade$rewrite.cookies.get(site);
  cookieCache = cookies.join('; ');
}

refreshCookies();

// best way i can find to get cookies working async
Object.defineProperty(document, 'cookie', {
  configurable: true,
  enumerable: true,
  get() {
    return cookieCache;
  },
  set(value: string) {
    self.__envade$rewrite.cookies
      .set(value, site)
      .then(() => {
        refreshCookies();
      })
      .catch(console.error);
  },
});
