const originalOpen = window.open;

window.open = function (url, target, features) {
  console.log(`Opening URL: ${url} with target: ${target} and features: ${features}`);
  let newUrl = new URL(url).toString();
  newUrl = self.__envade$rewrite.url.rewriteUrl(newUrl);

  return originalOpen.call(window, newUrl, '_self', features);
};
