import { GetLocation } from '../../rewriters/helper/getLocation';

const safeListenerMap = new Map();

const eventPatchers = {
  message(event) {
    const data = event.data;
    define(event, 'origin', {
      get() {
        return GetLocation().origin;
      },
    });
    define(event, 'data', {
      get() {
        return data;
      },
    });
  },
  messageerror(event) {
    const data = event.data;
    define(event, 'origin', {
      get() {
        return GetLocation().origin;
      },
    });
    define(event, 'data', {
      get() {
        return data;
      },
    });
  },
  hashchange(event) {
    const oldURL = event.oldURL;
    const newURL = event.newURL;
    define(event, 'oldURL', {
      get() {
        return self.__envade$rewrite.url.unwriteUrl(oldURL);
      },
    });
    define(event, 'newURL', {
      get() {
        return self.__envade$rewrite.url.unwriteUrl(newURL);
      },
    });
  },
  storage(event) {
    const key = event.key;
    const url = event.url;
    define(event, '_init', {
      value() {
        return key?.startsWith(GetLocation().host + '@');
      },
    });
    define(event, 'key', {
      get() {
        return key?.substring(key.indexOf('@') + 1) ?? null;
      },
    });
    define(event, 'url', {
      get() {
        return self.__envade$rewrite.url.unwriteUrl(url);
      },
    });
  },
};

function define(obj, key, descriptor) {
  try {
    Object.defineProperty(obj, key, {
      configurable: true,
      ...descriptor,
    });
  } catch {}
}

function createPatchedListener(type, fn) {
  if (!eventPatchers[type]) return fn;
  return new Proxy(fn, {
    apply(target, thisArg, args) {
      const [event] = args;
      if (event && typeof event === 'object') {
        try {
          eventPatchers[type](event);
        } catch {}
      }
      return Reflect.apply(target, thisArg, args);
    },
  });
}

window.addEventListener = new Proxy(window.addEventListener, {
  apply(target, thisArg, args) {
    const [type, fn, ...rest] = args;
    if (typeof fn !== 'function') return Reflect.apply(target, thisArg, args);
    if (!safeListenerMap.has(fn)) safeListenerMap.set(fn, new Map());
    const map = safeListenerMap.get(fn);
    if (!map.has(type)) map.set(type, createPatchedListener(type, fn));
    return Reflect.apply(target, thisArg, [type, map.get(type), ...rest]);
  },
});

window.removeEventListener = new Proxy(window.removeEventListener, {
  apply(target, thisArg, args) {
    const [type, fn, ...rest] = args;
    if (typeof fn !== 'function') return Reflect.apply(target, thisArg, args);
    const patched = safeListenerMap.get(fn)?.get(type) || fn;
    return Reflect.apply(target, thisArg, [type, patched, ...rest]);
  },
});

window.dispatchEvent = new Proxy(window.dispatchEvent, {
  apply(target, thisArg, args) {
    return Reflect.apply(target, thisArg, args);
  },
});
