import { GetLocation } from '../../../rewriters/helper/getLocation';

Object.defineProperty(document, 'domain', {
  get() {
    return GetLocation().hostname;
  },
  configurable: true,
});

['URL', 'baseURI', 'documentURI', 'referrer', 'origin'].forEach((prop) => {
  Object.defineProperty(document, prop, {
    get() {
      return GetLocation().href;
    },
    configurable: true,
  });
});

document.write = new Proxy(document.write, {
  apply(target, thisArg, args) {
    if (typeof args[0] === 'string') {
      try {
        const parser = new DOMParser();
        const doc = parser.parseFromString(args[0], 'text/html');

        doc.querySelectorAll('script').forEach(el => {
          el.textContent = self.__envade$rewrite.javascript(el.textContent || '', GetLocation().href);
        });

        doc.querySelectorAll('style').forEach(el => {
          el.textContent = self.__envade$rewrite.css(el.textContent || '', GetLocation().href);
        });

        args[0] = doc.documentElement.innerHTML;
      } catch {}
    }

    return Reflect.apply(target, thisArg, args);
  }
});

document.writeln = new Proxy(document.writeln, {
  apply(target, thisArg, args) {
    if (typeof args[0] === 'string') {
      try {
        const parser = new DOMParser();
        const doc = parser.parseFromString(args[0], 'text/html');

        doc.querySelectorAll('script').forEach(el => {
          el.textContent = self.__envade$rewrite.javascript(el.textContent || '', GetLocation().href);
        });

        doc.querySelectorAll('style').forEach(el => {
          el.textContent = self.__envade$rewrite.css(el.textContent || '', GetLocation().href);
        });

        args[0] = doc.documentElement.innerHTML;
      } catch {}
    }

    return Reflect.apply(target, thisArg, args);
  }
});



