function createStorageProxy(storage: Storage) {
  function filterBySite() {
    const prefix = `envade$${self.scope$location.host}@`;
    const keys: string[] = [];
    for (let i = 0; i < storage.length; i++) {
      const key = storage.key(i)!;
      if (key.startsWith(prefix)) keys.push(key);
    }
    return keys;
  }

  const prefix = `envade$${self.scope$location.host}@`;

  return new Proxy(storage, {
    get(target, prop) {
      switch (prop) {
        case 'setItem':
          return (key: string, value: string) => {
            target.setItem(prefix + key, value);
          };
        case 'getItem':
          return (key: string) => target.getItem(prefix + key);
        case 'removeItem':
          return (key: string) => target.removeItem(prefix + key);
        case 'clear':
          return () => {
            const keys = filterBySite();
            for (const key of keys) {
              target.removeItem(key);
            }
          };
        case 'length':
          return filterBySite().length;
        case 'key':
          return (index: number) => {
            const keys = filterBySite();
            return keys[index] ? keys[index].slice(prefix.length) : null;
          };
        default:
          return (target as any)[prop];
      }
    },
    ownKeys(target) {
      return filterBySite().map((k) => k.slice(prefix.length));
    },
    getOwnPropertyDescriptor(target, prop) {
      if (typeof prop === 'string') {
        const keys = filterBySite().map((k) => k.slice(prefix.length));
        if (keys.includes(prop)) {
          return {
            configurable: true,
            enumerable: true,
            value: target.getItem(prefix + prop),
            writable: true,
          };
        }
      }
      return undefined;
    },
  });
}

const local = createStorageProxy(window.localStorage);
const session = createStorageProxy(window.sessionStorage);

Object.defineProperty(window, 'localStorage', {
  value: local,
  configurable: true,
  writable: false,
  enumerable: true,
});

Object.defineProperty(window, 'sessionStorage', {
  value: session,
  configurable: true,
  writable: false,
  enumerable: true,
});
